import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Hamburger from 'hamburger-react';  //    
import axios from 'axios';  //    
import './Header.css'

const Header: React.FC = () => {
  const [username, setUsername] = useState<string | null>(null);
  const [isMenuOpen, setIsMenuOpen] = useState(false); //    -
  const [error, setError] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token) {
      //     
      axios
        .get('/api/user/profile', {
          headers: { Authorization: `Bearer ${token}` }, //    
        })
        .then((response) => {
          console.log('User data from server:', response.data); //   
          setUsername(response.data.username); //     
        })
        .catch((err) => {
          setError('Error fetching user data');
          console.error(err);
        });
    } else {
      setError('No token found, please log in');
    }
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('token'); //  
    navigate('/login');  //    
  };

   //        
   const handleLinkClick = () => {
    setIsMenuOpen(false); //    
  };

  return (
    <header className='header'>
      <div className='logo'>
        <Link className='logo-text' to="/">UdiLang</Link>
      </div>

      {/*  -   */}
      <div className='burgerMenuButton' onClick={() => setIsMenuOpen(!isMenuOpen)}>
        <Hamburger toggled={isMenuOpen} toggle={setIsMenuOpen} />
      </div>

      {/* - */}
      <nav className={isMenuOpen ? 'nav open' : 'nav'}>
          
        <ul className='navList'>
        <li className='navItem'>    
          <div className='userSelection'>
            {username ? <p className='username'>, {username.slice(0,1).toUpperCase()}{username.slice(1)}!</p> : null}
            {error && <p className='error'>{error}</p>}
          </div>
          </li>
          <li className='navItem'>    
            <Link to="/profile" className='navLink' onClick={handleLinkClick}> </Link>
          </li>

          <li className='navItem'>
            <Link to="/" className='navLink' onClick={handleLinkClick}></Link>
          </li>
          <li className='navItem'>
            <Link to="/dictionary" className='navLink' onClick={handleLinkClick}></Link>
          </li>
          <li className='navItem'>
            <Link to="/add-word" className='navLink' onClick={handleLinkClick}> </Link>
          </li>
          <li className='navItem'>
            <Link to="/add-translation" className='navLink' onClick={handleLinkClick}> </Link>
          </li>
        </ul>
        <button onClick={handleLogout} className='logoutButton'></button>

      </nav>
    </header>
  );
};

export default Header;
