import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './ProfilePage.css'

const ProfilePage: React.FC = () => {
  const [user, setUser] = useState<{ username: string; email: string; created_at: string } | null>(null);
  const [userStats, setUserStats] = useState<{ total: number; translated: number }>({
    total: 0,
    translated: 0,
  });
  const [error, setError] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      //   ,    
      navigate('/login');
    } else {
      //     
      axios
        .get('api/user/profile', {
          headers: { Authorization: `Bearer ${token}` }, //    
        })
        .then((response) => {
          setUser(response.data);  //    
        })
        .catch((err) => {
          setError('Error fetching user data');
          console.error(err);
        });

      //    (    )
      axios
        .get('api/user/stats', {
          headers: { Authorization: `Bearer ${token}` },
        })
        .then((response) => {
          setUserStats(response.data); //  
        })
        .catch((err) => {
          setError('Error fetching user statistics');
          console.error(err);
        });
    }
  }, [navigate]);

  const handleLogout = () => {
    localStorage.removeItem('token');
    navigate('/login');
  };

  return (
    <div className='page-wrapper'>
      <h1 className='section-title'> </h1>
      {error && <p style={{ color: 'red' }}>{error}</p>}
      {user ? (
        <div className='profile-wrapper'>
          <div className="profile-top">
            <div className='profile-picture'></div>
            <div>
              <p className='profile-name'>{user.username.slice(0,1).toUpperCase()}{user.username.slice(1)}</p>
              <p className='profile-email'>: {user.email}</p>
              {/*       Date    */}
              <p className='profile-join-date'>
                 UdiLang  {user.created_at ? new Date(user.created_at).toLocaleDateString() : 'Invalid Date'}
              </p>
            </div>
          </div>
          <p className='profile-stat'>
            {`    ${userStats.translated} `}
          </p>
          <button className='profile-logout' onClick={handleLogout}></button>
        </div>
      ) : (
        <p>Loading...</p>
      )}
    </div>
  );
};

export default ProfilePage;
